@use 'variable' as config;

@font-face {
	font-family: config.$font-base-family;
	font-weight: config.$font-light;
	font-style: config.$font-base-style;
	font-display: block;
	src: url('#{config.$path-font}/Pretendard-Light.subset.woff2') format('woff2'),
		url('#{config.$path-font}/Pretendard-Light.subset.woff') format('woff');
}
@font-face {
	font-family: config.$font-base-family;
	font-weight: config.$font-regular;
	font-style: config.$font-base-style;
	font-display: block;
	src: url('#{config.$path-font}/Pretendard-Regular.subset.woff2') format('woff2'),
		url('#{config.$path-font}/Pretendard-Regular.subset.woff') format('woff');
}
